.userList {
    flex: 5;
    margin-left: 15px;
}

.userListUser {
    display: flex;
    align-items: center;
}

.userListImg {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    margin-right: 10px;
    object-fit: cover;
}

.userListEdit{
    border: none;
    border-radius: 5px;
    align-items: center;
    justify-content: center;
    padding: 5px 10px;
    color: #fff;
    background-color: #3bb077;
    margin-right: 20px;
    cursor: pointer;
}

.userListDelete{
    color: red;
    cursor: pointer;
}